.minPlayer {
  font-size: 14px;
  height: 55px;
  padding: 0 10px 0 80px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  box-shadow: 0 -2px 2px #e9e9e9;
  background-color: #ffffff;
}
.minPlayer .minPlayer-img {
  float: left;
  width: 60px;
  height: 60px;
  margin-left: -70px;
  margin-top: -10px;
  border-radius: 50%;
}
.minPlayer .minPlayer-img img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0 0 10px #999;
}
.minPlayer .minPlayer-ct {
  position: relative;
  margin-top: 3px;
  padding-right: 118px;
}
.minPlayer .minPlayer-ct .title {
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.minPlayer .minPlayer-ct .desc {
  font-size: 12px;
  line-height: 18px;
  color: #ababab;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.minPlayer .minPlayer-ct .minPlayer-ct-btn {
  position: absolute;
  right: 0;
  top: 5px;
}
.minPlayer .minPlayer-ct .minPlayer-ct-btn i {
  display: inline-block;
  vertical-align: middle;
  font-size: 26px;
  width: 36px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: #555555;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.minPlayer .minPlayer-ct .minPlayer-ct-btn i.van-icon-upgrade {
  display: inline-block;
  transform: rotate(90deg);
}
.minPlayer .minPlayer-ct .minPlayer-ct-btn i.van-icon-play-circle-o {
  color: #23e379;
}
.minPlayer .minPlayer-ct .minPlayer-ct-btn i.van-icon-pause-circle-o {
  color: #23e379;
}
.minPlayer-rangeBox {
  padding-top: 8px;
}
.van-slider__bar {
  background-color: #23e379 !important;
}
.minPlayer-range {
  margin: 0 !important;
}
.minPlayer-range .van-slider__button {
  width: 8px;
  height: 8px;
}
.minPlayer-range .range-bar-disabled {
  opacity: 1;
}
.minPlayer-range .range-min,
.minPlayer-range .range-max {
  display: none;
}
.minPlayer-range .range-handle {
  background-color: #51ccff;
  width: 8px;
  height: 8px;
  top: -3px !important;
}
.minPlayer-range .range-quantity {
  background-color: #51ccff;
}
.van-popup .van-icon-exchange {
  color: #333 !important;
}
.van-popup .van-nav-bar__text {
  color: #333;
}
.van-popup .van-nav-bar__text span {
  color: #7d7e80;
  font-size: 12px;
}
.van-popup .van-address-item {
  padding: 10px 15px;
}
.van-popup .van-address-item__edit {
  color: #23e379;
}
.van-popup .van-popup {
  overflow: inherit;
}
.van-popup .van-radio__box {
  top: 50%;
  left: 0;
  height: 16px;
  position: absolute;
  line-height: 16px;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
}
.van-popup .van-radio__box .rank-des {
  display: inline-block;
}
.van-popup .van-radio__box .rank-des .van-icon-music-o {
  color: #23e379;
}
